import './style.css';

import { Path } from "./path.js";
import { CloudController } from "./cloud-controller.js";

import { Background } from "./background.js";

class App {
  constructor() {

    this.backBtn = document.createElement("div");
    this.backBtn.classList.add("back-btn");

    this.backA = document.createElement("a");
    this.backA.classList.add("back-btn__link");
    this.backA.setAttribute("href", "/");
    this.backSvg = document.createElementNS(
      "http://www.w3.org/2000/svg",
      "svg"
    );
    this.backSvg.classList.add("back-btn__svg");
    this.backSvg.setAttribute("xmlns", "http://www.w3.org/2000/svg");
    this.backSvg.setAttributeNS(null, "viewBox", "0 0 24 24");
    this.backPath = document.createElementNS(
      "http://www.w3.org/2000/svg",
      "path"
    );
    this.backPath.setAttributeNS(
      null,
      "d",
      "M20 11H7.83l5.59-5.59L12 4l-8 8 8 8 1.41-1.41L7.83 13H20v-2z"
    );

    this.backSpan = document.createElement("span");
    this.backSpan.classList.add("back-btn__text");
    this.backSpan.innerHTML = "Back to noon"

    this.backSvg.appendChild(this.backPath);
    this.backA.append(this.backSvg, this.backSpan);
    this.backBtn.appendChild(this.backA);
    
    document.body.appendChild(this.backBtn);

    this.instruction = document.createElement("div");
    this.instruction.classList.add("instruction", "hidden", "visuallyhidden");
    this.instructionSvg = document.createElementNS(
      "http://www.w3.org/2000/svg",
      "svg"
    );
    this.instructionSvg.classList.add("instruction__svg");
    this.instructionSvg.setAttribute("xmlns", "http://www.w3.org/2000/svg");
    this.instructionSvg.setAttributeNS(null, "viewBox", "0 0 24 24");
    this.instructionPath = document.createElementNS(
      "http://www.w3.org/2000/svg",
      "path"
    );
    this.instructionPath.setAttributeNS(
      null,
      "d",
      "M18.19,12.44l-3.24-1.62c1.29-1,2.12-2.56,2.12-4.32c0-3.03-2.47-5.5-5.5-5.5s-5.5,2.47-5.5,5.5c0,2.13,1.22,3.98,3,4.89 v3.26c-2.15-0.46-2.02-0.44-2.26-0.44c-0.53,0-1.03,0.21-1.41,0.59L4,16.22l5.09,5.09C9.52,21.75,10.12,22,10.74,22h6.3 c0.98,0,1.81-0.7,1.97-1.67l0.8-4.71C20.03,14.32,19.38,13.04,18.19,12.44z M17.84,15.29L17.04,20h-6.3 c-0.09,0-0.17-0.04-0.24-0.1l-3.68-3.68l4.25,0.89V6.5c0-0.28,0.22-0.5,0.5-0.5c0.28,0,0.5,0.22,0.5,0.5v6h1.76l3.46,1.73 C17.69,14.43,17.91,14.86,17.84,15.29z M8.07,6.5c0-1.93,1.57-3.5,3.5-3.5s3.5,1.57,3.5,3.5c0,0.95-0.38,1.81-1,2.44V6.5 c0-1.38-1.12-2.5-2.5-2.5c-1.38,0-2.5,1.12-2.5,2.5v2.44C8.45,8.31,8.07,7.45,8.07,6.5z"
    );

    this.instructionSvg.appendChild(this.instructionPath)
    this.instruction.appendChild(this.instructionSvg)
    document.body.appendChild(this.instruction)

    this.mouseCursor = document.createElement("div");
    this.mouseCursor.className = "cursor";
    document.body.appendChild(this.mouseCursor);

    this.canvas = document.createElement("canvas");
    this.ctx = this.canvas.getContext("2d");
    document.body.appendChild(this.canvas);

    this.logo = document.createElement("IMG");
    this.logo.classList.add("logo");
    this.logo.setAttribute("src", "klm_logo_white.png");
    this.logo.setAttribute("alt", "KLM Logo");
    document.body.appendChild(this.logo);

    this.paths = [];

    this.cloudController = new CloudController();
    this.background = new Background();

    this.pixelRatio = window.devicePixelRatio > 1 ? 2 : 1;

    document.addEventListener("pointermove", this.onMove.bind(this), false);
    window.addEventListener("pointerup", this.onUp.bind(this), false);
    window.addEventListener("resize", this.resize.bind(this), false);

    this.resize();

    requestAnimationFrame(this.animate.bind(this));
  }

  resize() {
    this.stageWidth = document.body.clientWidth;
    this.stageHeight = document.body.clientHeight;

    this.canvas.width = this.stageWidth * this.pixelRatio;
    this.canvas.height = this.stageHeight * this.pixelRatio;
    this.ctx.scale(this.pixelRatio, this.pixelRatio);

    this.background.backgroundColor(this.paths);

    this.cloudController.resize(this.stageWidth, this.stageHeight);

    for (let i = 0; i < this.paths.length; i++) {
      this.paths[i].resize(this.stageWidth, this.stageHeight);
    }

    this.showInstruction();
  }

  showInstruction() {
    let id;
    clearInterval(id);
    id = setInterval(frame.bind(this), 1000);
    function frame() {
      setTimeout(function() {
          this.instruction.style.transform = `translate(${this.stageWidth * Math.random()}px, ${this.stageHeight * Math.random()}px)`;
      }.bind(this), 1000);
    }
    this.instruction.classList.remove('hidden'); 
    setTimeout(function() {
      this.instruction.classList.remove('visuallyhidden');
    }.bind(this), 1000);

    setTimeout(function() {
      this.instruction.classList.add('visuallyhidden');
    }.bind(this), 8000);
    setTimeout(function() {
      this.instruction.classList.add('hidden');
      clearInterval(id);
    }.bind(this), 8500);
  }

  animate(t) {
    requestAnimationFrame(this.animate.bind(this));

    this.ctx.clearRect(0, 0, this.stageWidth, this.stageHeight);

    let end = Boolean;

    this.background.backgroundColor(this.paths);

    this.paths.length > 0 && this.cloudController.draw(this.ctx);

    for (let i = 0; i < this.paths.length; i++) {
      end = this.paths[i].draw(this.ctx, t);
      end && this.paths.splice(i, 1);
    }
  }

  onUp(e) {
    let cx = e.clientX;
    let cy = e.clientY;

    this.paths.push(
      new Path(this.ctx, cx, cy, this.stageWidth, this.stageHeight)
    );

    this.background.backgroundColor(this.paths);
  }

  onMove(e) {
    this.mouseCursor.style.transform = `translate(${
      e.clientX - this.mouseCursor.offsetWidth / 2
    }px, ${e.clientY - this.mouseCursor.offsetHeight / 2}px)`;
  }
}

window.onload = () => {
  new App();
};
